import {Component,OnInit} from '@angular/core';
import { Router } from '@angular/router';
import {NgForm}    from '@angular/common';

import { Hero }    from './hero';
import { Power } from './power';
import { PowerService } from '../service/power.service';
import { HeroService } from '../service/hero.service';

@Component({
  selector: 'hero-form',
  templateUrl: 'app/hero-portal/hero/hero-form.component.html',
  styles:[
    `
    .ng-valid[required] {
      border-left: 5px solid #42A948; /* green */
    }

    .ng-invalid {
      border-left: 5px solid #a94442; /* red */
    }
    `
  ],
  directives:[NgForm],
  providers:[PowerService,HeroService]
})
export class HeroFormComponent implements OnInit{
    submibtted=false;
    data:string;
    onSubmit(d:any){
       this.data = JSON.stringify(d, null, 2);
    }
    constructor(private _pService:PowerService,
                private _hService:HeroService,
                private _r:Router){}
    powers:Power[];
    heroes:Hero[];
    model:Hero=new Hero(999,"","");
    maxId:number=0;
    ngOnInit(){
      
      this._pService
          .getPowersSlowly()
          .then(powers=>
                {
                  this.powers=powers;
                });
      this._hService
          .getMaxIdHero()
          .then(num=>{
                this.maxId=num;
                this.model.id=this.maxId;
          });
    }
    
    get diagnostic() { return JSON.stringify(this.model); }
    
    goBack(){
        this._r.navigate(['/hero-portal/hero']);
    }
}